<template>
  <div class=''>
    <div id='chart' class='line-chart'></div>
  </div>
</template>
<script>
// 引入 ECharts 主模块
// const echarts = require('echarts')
// require('echarts/lib/chart/line')
export default {
  name: 'LineChart',
  props: ['data', 'maxValue'],
  components: {},
  data () {
    return {
      timeRanges: ['空腹', '早餐后', '午餐前', '午餐后', '晚餐前', '晚餐后', '睡前']
    }
  },
  computed: {
    status: function () {
      return this.$store.getters.lineSendStatus
    }
  },
  methods: {
    setLineChart (datas) {
      let data = []
      let type = 'line'
      let timeMax = new Date(parseInt(datas[0].Time.slice(6, 19)))
      let timeMin = new Date(parseInt(datas[0].Time.slice(6, 19)))
      for (let i = 0; i < datas.length; i++) {
        let temp = datas[i]
        let tempTime = new Date(parseInt(temp.Time.slice(6, 19)))
        if (tempTime > timeMax) {
          timeMax = tempTime
        }
        if (tempTime < timeMin) {
          timeMin = tempTime
        }
        let itemColor = 'black'
        if (temp.Value > 0 && temp.Value <= 3.9) {
          itemColor = '#F40000'
        } else if ((temp.Value >= 7 && temp.TimeRange === 0) || temp.Value > 10) {
          itemColor = '#4D92E3'
        } else {
          itemColor = '#A0A0A0'
        }
        data.push({
          value: [parseInt(temp.Time.slice(6, 19)), temp.Value],
          timeRange: this.timeRanges[temp.TimeRange],
          itemStyle: {
            normal: { color: itemColor }
          },
          emphasis: {
            itemStyle: {
              borderColor: itemColor
            }
          }
        })
      }
      if (timeMax - timeMin < 7) {
        timeMin = timeMax - 7
      }
      this.DrawChart(data, type, timeMax, timeMin)
    },
    areaStyle (color, value, timeMax, timeMin, width, type, opacity) {
      let option = {
        type: 'line',
        areaStyle: {
          normal: {
            color: color,
            opacity: opacity
          }
        },
        itemStyle: {
          normal: {
            opacity: 0
          }
        },
        lineStyle: {
          normal: {
            width: width,
            color: color,
            type: type
          }
        },
        data: [[timeMax, value], [timeMin, value]]
      }
      return option
    },
    DrawChart (data, type, timeMax, timeMin) {
      let chart = document.getElementById('chart')
      let standardRateChart = echarts.init(chart)
      const maxValue = Math.ceil(this.maxValue / 5) * 5
      let startValue = data[0].value[0] - 3600 * 24 * 1000 * 7
      var series = [{
        symbol: 'circle',
        symbolSize: 5,
        type: type,
        lineStyle: {
          normal: {
            width: 1,
            color: '#999'
          }
        },
        itemStyle: {
          borderWidth: 5,
          borderColor: 'rgba(0, 0, 0, 0)'
        },
        data: data
      },
      this.areaStyle('#F4FAFE', maxValue, timeMax, timeMin, 0, 'solid', 1),
      this.areaStyle('#FFFFFF', 10, timeMax, timeMin, 0, 'solid', 1),
      this.areaStyle('#fdf4f4', 3.9, timeMax, timeMin, 0, 'solid', 1)]
      for (let i = 1; i <= Math.ceil(maxValue / 5); i++) {
        series.push(this.areaStyle('#ccc', 5 * i, timeMax, timeMin, 0.3, 'dashed', 0))
      }
      let option1 = {
        animation: false,
        grid: {
          top: '3%'
        },
        xAxis: {
          type: 'time',
          splitLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#7C8493',
              width: 2
            }
          },
          axisLabel: {
            showMinLabel: true,
            showMaxLabel: true,
            formatter: function (val, index) {
              const date = new Date(val)
              const dateStr = (date.getMonth() + 1) + '月' + date.getDate() + '日'
              return dateStr
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: false
          },
          min: -0.1,
          zlevel: 9,
          axisTick: {
            show: false
          },
          interval: 0.1,
          axisLabel: {
            formatter (val) {
              const arr = [0, 3.9, 5, 7, 10, 15, 20, 25, 30, 35, 40, 45, 50, 55, 60, 65]
              const index = arr.indexOf(val)
              if (index > -1) {
                return arr[index]
              } else {
                return ''
              }
            }
            // inside: true
          },
          splitLine: {
            show: false
          }
        },
        dataZoom: [
          {
            type: 'inside',
            filterMode: 'none',
            minValueSpan: 3600 * 24 * 1000 * 7,
            startValue: startValue
          }
        ],
        tooltip: {
          triggerOn: 'click',
          formatter: function (params, ticket, callback) {
            const data = params.data
            const date = new Date(data.value[0])
            const dateStr = (date.getMonth() + 1) + '月' + date.getDate() + '日'
            return (
              dateStr +
              '<br />' +
              data.timeRange +
              '&nbsp;&nbsp;' +
              Math.round(data.value[1] * 10) / 10
            )
          }
        },
        series: series
      }
      standardRateChart.setOption(option1, true)
    }
  },
  watch: {
    status: function (val, oldVal) {
      if (val && this.data.length) {
        this.setLineChart(this.data)
      }
    }
  },
  mounted () {
    if (this.status && this.data && this.data.length) {
      this.setLineChart(this.data)
    }
  }
}
</script>
<style lang='scss' scoped>
@import './style.scss';
</style>



