<template>
  <div class="page-wrp">
    <div class="page-invite"
      :style="{
        paddingTop: pageTopHeight + 'px',
        paddingBottom: ($route.name !== 'PatientDetail') ? '5px' : '',
        background: $route.params.hcpId || !$route.params.id ? '#E9E7E6' : ''}">
      <div class="page-top" ref="top">
        <h1 class="page-title" v-if="$route.params.hcpId && $route.params.id">
          {{departWatchDoctor.DoctorName ? departWatchDoctor.DoctorName + ' ' : ''}}的患者
        </h1>
        <section class="patient-info clearfix" :style="{marginBottom: patient.Te_Scheme0 || patient.Te_insulin ? '0' : ''}">
          <div class="patient-photo">
            <img :src="patient.photo" alt="">
          </div>
          <div class="patient-text">
            <p class="clearfix">
              <span class="patient-name">
                {{patient.Name}}
              </span>
              <span>{{patient.sex}}</span>
              <span>{{patient.age}}岁</span>
            </p>
            <p class="clearfix">
              <span>
                {{patient.enrollDays}}
              </span>
            </p>
          </div>
          <div class="like-icon" :class="patient.IsStar ? 'like' : 'unlike'" v-if="!$route.params.hcpId && ($route.name !== 'patientShare')" @click="isChangeLike ? '' : handleLike(patient)">
            <icon :name="patient.IsStar ? 'star' : 'star-o'" scale="1.38"></icon>
          </div>
        </section>
        <section class="medicine-intro clearfix" v-if="patient.Te_Scheme0 || patient.Te_insulin">
          <div class="intro-text">
            <p class="intro-text-top">{{patient.Te_insulin || '未知'}}</p>
            <p v-if="patient.Te_Scheme0">{{patient.Te_Scheme0}}</p>
          </div>
          <div v-if="patient.Te_Scheme0" class="intro-link" @click="togglePop(patient.Te_Updated_Date + '治疗方案')">
            <p><img src="../../../assets/PatientDetail/icon_file.png" alt=""></p>
            <p>详情</p>
          </div>
        </section>
      </div>
      <section class="test-detail">
        <div class="detail-text">
          <p>
            {{patient.testCount ? '近7天测量' + patient.testCount + '次' : '近7天无测量'}}
            <span v-show="patient.lowCount">低血糖{{patient.lowCount}}次</span>
            <span class="intro" @click="togglePop('说明')">
              说明
              <i>
                <icon name="question-circle-o"></icon>
              </i>
            </span>
          </p>
          <p class="tag-wrp">
            <span v-for="(tag, index) in patient.tags"
              :style="{color: tag === '高血糖' ? '#76ABE9' : '',
                border: tag === '高血糖' ? '1px solid #CDE5F6' : '1px solid #FFCCBD',
                background: tag === '高血糖' && currentTag === '高血糖' ? 'rgba(123,174,234,0.3)' : ''}"
              :key="index" class="tag"
              :class="tag === currentTag ? 'tag-active' : ''"
              @click="dataStyle === 'CalendarSheet' ? selectTag(tag) : ''">
              <img
                :src="tag !== '极低血糖' ? (tag === '高血糖' ? highIcon : low3Icon) : lowIcon" alt="">
              {{tag}}
            </span>
          </p>
        </div>
        <div class="detail-chart">
          <ul class="tab-buttons clearfix">
            <li class="tab-button" :class="dataStyle === item.value ? 'tab-active' : ''" v-for="(item, index) in tabs" :key="index" @click="changeTab(item.value)">{{item.text}}</li>
          </ul>
          <component
            :currentTag="currentTag"
            :enrollDaysNum="patient.enrollDaysNum"
            :maxValue="patient.maxValue"
            :data="dataStyle === 'CalendarSheet' ? patient.calendarData : patient.lineData"
            :is="dataStyle"></component>
        </div>
      </section>
      <div class="share-footer"  v-if="($route.name === 'patientShare') && patient.ShowQRCode">
        <div class="footer-text">
          <p>长按识别二维码</p>
          <p>可联系优泌乐系列胰岛素制造和血</p>
          <p>糖管理服务提供方礼来制药公司</p>
        </div>
        <div class="footer-share">
          <img src="../../../assets/PatientDetail/qr.jpg">
        </div>
      </div>
      <footer v-if="($route.name !== 'DepartPatientDetail') && ($route.name !== 'patientShare')">
        <div class="actions">
          <ul class="buttons">
            <li v-for="(item, index) in actions" :class="patient.IsInteraction ? 'disabled' : ''" :key="index" @click="sendMsg(item)">
              <img :src="item.img" alt="">
              <!-- <br>
              {{item.text}} -->
            </li>
          </ul>
        </div>
        <div class="talk" @click="goToPocket">
          <span><img src="../../../assets/PatientDetail/new_msg.png" alt="">发起会话</span>
        </div>
      </footer>
    </div>
    <Loading :showLoading="showLoading" />
    <Modal :title="popTitle" :showPop="showPop" :close="togglePop" :autoHeight="popTitle === '说明'">
      <div>
        <div class="rule" v-if="popTitle === '说明'">
          <p class="tag-row">
            <span data-v-95ac62fc="" class="tag" style="border: 1px solid rgb(255, 204, 189);">
              <img :src="lowIcon" alt="">
              极低血糖
            </span>
          </p>
          <p class="rule-text">血糖值≤3.0mmol/L</p>
          <p class="tag-row">
            <span data-v-95ac62fc="" class="tag" style="color: rgb(118, 171, 233); border: 1px solid rgb(205, 229, 246);">
              <img :src="highIcon" alt="">
              高血糖
            </span>
          </p>
          <ol class="rule-text">
            <li>1.血糖值≥16.7mmol/L</li>
            <li>2.周测试频率≤5次且50%测量点为高血糖<br><span>(血糖值≥13.9mmol/L)</span></li>
            <li>3.过去7天内出现不少于3次高血糖(血糖值<br><span>≥13.9mmol/L)</span></li>
          </ol>
          <p class="tag-row">
            <span data-v-95ac62fc="" class="tag" style="border: 1px solid rgb(255, 204, 189);">
              <img :src="low3Icon" alt="">
              连续低血糖
            </span>
          </p>
          <ol class="rule-text">
            <li>
              1.连续3天出现低血糖(血糖值<br><span>≤3.9 mmol/L)</span>
            </li>
            <li>
              2.周测试频率≤5次且50%测量点为低血糖<br><span>(血糖值≤3.9mmol/L)</span>
            </li>
            <li>
              3.过去7天内出现不少于3次低血糖 (血糖<br><span>值≤3.9 mmol/L)</span>
            </li>
          </ol>
        </div>
        <div class="plan" v-if="popTitle !== '说明'">
          <section class="patient-info clearfix">
            <div class="patient-photo">
              <img :src="patient.photo" alt="">
            </div>
            <div class="patient-text">
              <p class="clearfix">
                <span class="patient-name">
                  {{patient.Name}}
                </span>
                <span>{{patient.sex}}</span>
                <span>{{patient.age}}岁</span>
              </p>
              <p class="clearfix">
                <span>
                  {{patient.enrollDays}}
                </span>
              </p>
            </div>
          </section>
          <div class="medicine">
            <div>
              <p class="title">胰岛素品规</p>
              <p class="text">{{patient.Te_insulin ? patient.Te_insulin : '未知'}}</p>
            </div>
            <div>
              <p class="title">注射方案</p>
              <p class="text">{{patient.Te_Scheme ? patient.Te_Scheme : '未知'}}</p>
              <p class="time">{{patient.Te_Scheme1}}</p>
            </div>
          </div>
          <div class="specific">
            <ul class="top">
              <li>早餐前</li>
              <li>午餐前</li>
              <li>晚餐前</li>
              <li>睡前</li>
              <li>日总剂量</li>
            </ul>
            <ul class="num">
              <li>{{patient.Te_Before_breakfast ? patient.Te_Before_breakfast + '单位' : '- -'}}</li>
              <li>{{patient.Te_Before_lunch ? patient.Te_Before_lunch + '单位' : '- -'}}</li>
              <li>{{patient.Te_Before_dinner ? patient.Te_Before_dinner + '单位' : '- -'}}</li>
              <li>{{patient.Te_Before_sleep ? patient.Te_Before_sleep + '单位' : '- -'}}</li>
              <li class="num-count">{{patient.Te_Day_amounts ? patient.Te_Day_amounts + '单位' : '- -'}}</li>
            </ul>
          </div>
          <ul class="person-info">
            <li>身高
              <p>{{patient.Te_Height ? patient.Te_Height + 'cm' : '未知'}}</p>
            </li>
            <li>体重
              <p>{{patient.Te_Weight ? patient.Te_Weight + 'kg' : '未知'}}</p>
            </li>
            <li>BMI
              <p>{{patient.BMI ? patient.BMI : '未知'}}</p>
            </li>
          </ul>
          <div class="count">
            <ul>
              <li>每周建议测量次数
                <p>{{patient.Te_Week_total_number ? patient.Te_Week_total_number : '未知'}}</p>
              </li>
              <li>复诊前一天测量次数
                <p>{{patient.Te_Day_before_number ? patient.Te_Day_before_number : '未知'}}</p>
              </li>
            </ul>
            <ul>
              <li>口服药治疗
                <p>{{patient.Te_Is_oral_medicine === 2 ? '否' : (patient.Te_Is_oral_medicine === 1 ? '是' : '未知')}}</p>
              </li>
              <li>最近一次糖化血红蛋白
                <p>{{patient.Te_Hba1c ? patient.Te_Hba1c + '%' : '未知'}}</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </Modal>
    <ConfirmToMsgModal id="ConfirmToMsgModal" :showConfirm="showConfirmToMsg" :patient="patient" :close="closeConfirmToMsg" />
    <ConfirmModal :closeModal="closeModal" :showConfirm="showConfirm" :sendInfo="sendInfo" :hasSend="patient.IsInteraction" :patientId="patient.PatientId" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import CalendarSheet from './CalendarSheet'
import Loading from '../../Shared/Loading/Loading'
import lowIcon from '../../../assets/HomeView/low.png'
import low3Icon from '../../../assets/HomeView/low3.png'
import highIcon from '../../../assets/HomeView/high.png'
import likeActionImg from '../../../assets/PatientDetail/like.svg'
import testActionImg from '../../../assets/PatientDetail/test.svg'
import tipActionImg from '../../../assets/PatientDetail/tip.svg'
import ConfirmModal from '../../Shared/ConfirmModal/ConfirmModal'
import ConfirmToMsgModal from '../../Shared/ConfirmToMsgModal/ConfirmToMsgModal'
import Modal from '../../Shared/Modal/Modal'
import LineChart from './LineChart'
export default {
  name: 'PatientDetail',
  components: {
    CalendarSheet,
    LineChart,
    Loading,
    ConfirmModal,
    ConfirmToMsgModal,
    Modal
  },
  data () {
    return {
      tabs: [
        {
          text: '血糖日历表',
          value: 'CalendarSheet'
        },
        {
          text: '血糖图',
          value: 'LineChart'
        }
      ],
      actions: [
        {
          text: '提醒复诊',
          value: '1',
          img: tipActionImg
        },
        {
          text: '鼓励一下',
          value: '0',
          img: likeActionImg
        },
        {
          text: '提醒测量',
          value: '2',
          img: testActionImg
        }
      ],
      lowIcon: lowIcon,
      low3Icon: low3Icon,
      highIcon: highIcon,
      dataStyle: 'CalendarSheet',
      showLoading: false,
      showConfirm: false,
      showConfirmToMsg: false,
      showPop: false,
      isChangeLike: false,
      popTitle: '',
      currentTag: '',
      sendInfo: {},
      pageTopHeight: 0
    }
  },
  computed: {
    ...mapGetters({
      patient: 'patientItem',
      patientSendStatus: 'patientSendStatus',
      calendarSendStatus: 'calendarSendStatus',
      lineSendStatus: 'lineSendStatus',
      departWatchDoctor: 'departWatchDoctor',
      chatUrl: 'chatUrl'
    })
  },
  methods: {
    changeTab (tab) {
      if (this.dataStyle === tab) {
        return
      }
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'Click ' + tab})
      this.dataStyle = tab
      if ((tab === 'LineChart') && (this.lineSendStatus === 0)) {
        this.showLoading = true
        if (this.$route.name === 'patientShare') {
          this.$store.dispatch('fetchDetailShareData', {tab: 1})
        } else {
          this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 1})
        }
        this.currentTag = ''
      } else if (!this.calendarSendStatus) {
        this.showLoading = true
        if (this.$route.name === 'patientShare') {
          this.$store.dispatch('fetchDetailShareData', {tab: 0})
        } else {
          this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 0})
        }
      }
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: tab})
    },
    handleLike (patient) {
      this.isChangeLike = true
      var isLike = patient.IsStar ? '1' : '0'
      this.$store.dispatch('handleLike', {patientId: patient.PatientId, isLike: isLike, from: 'PatientDetail'})
      const msg = patient.IsStar ? '已将' + patient.Name + '从重点关注中移除' : '已将' + patient.Name + '加入重点关注'
      this.$toast(msg, 'center')
      setTimeout(() => {
        this.isChangeLike = false
      }, 600)
    },
    selectTag (tag) {
      if (this.currentTag === tag) {
        this.currentTag = ''
      } else {
        this.currentTag = tag
      }
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'Click Tag: ' + tag})
    },
    goToPocket () {
      this.showConfirmToMsg = true
    },
    sendMsg (obj) {
      if (this.patient.IsInteraction) {
        // this.$toast('患者还未收到血糖仪', 'center')
        this.$toast('您每天只能向患者发送一次' + obj.text + '信息，下次可发送时间为次日00:00。', 'center')
        return
      }
      this.sendInfo = obj
      this.showConfirm = true
    },
    closeModal () {
      this.showConfirm = false
    },
    closeConfirmToMsg () {
      this.showConfirmToMsg = false
    },
    togglePop (title) {
      var message = (title === '说明' ? 'introduction' : 'detail')
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: message})
      this.popTitle = title
      this.showPop = !this.showPop
    }
  },
  watch: {
    lineSendStatus: function (val, oldVal) {
      if (this.dataStyle === 'LineChart') {
        this.showLoading = val === 0
      }
    },
    calendarSendStatus: function (val, oldVal) {
      if (this.dataStyle === 'CalendarSheet') {
        this.showLoading = val === 0
      }
    },
    patient: function (val, oldVal) {
      if (val.PatientId !== undefined && !oldVal.PatientId) {
        // 防止页面刷新导致的padding样式问题
        this.pageTopHeight += 40
        if (this.patient.BGData.length <= 2) {
          this.dataStyle = 'LineChart'
          if (this.$route.name === 'patientShare') {
            this.$store.dispatch('fetchDetailShareData', {tab: 1})
          } else {
            this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 1})
          }
        } else {
          if (this.$route.name === 'patientShare') {
            this.$store.dispatch('fetchDetailShareData', {tab: 0})
          } else {
            this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 0})
          }
        }
      }
    }
  },
  beforeMount () {
    this.showLoading = true
    if (!this.patient.PatientId && this.$route.name === 'patientShare') {
      this.$store.dispatch('fetchPatientByShare')
    } else if (!this.patient.PatientId) {
      if (this.$route.params.hcpId) {
        this.$store.dispatch('fetchPatientItem', {hcpId: this.$route.params.hcpId, patientId: this.$route.params.id})
      } else {
        this.$store.dispatch('fetchPatientItem', this.$route.params.id)
      }
    } else {
      if (this.patient.BGData.length <= 2) {
        this.dataStyle = 'LineChart'
        if (this.$route.name === 'patientShare') {
          this.$store.dispatch('fetchDetailShareData', {tab: 1})
        } else {
          this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 1})
        }
      } else {
        if (this.$route.name === 'patientShare') {
          this.$store.dispatch('fetchDetailShareData', {tab: 0})
        } else {
          this.$store.dispatch('fetchDetailData', {patientId: this.$route.params.id, tab: 0})
        }
      }
    }
    // fix: ios10口袋医生后退不刷新，仍显示中转页
    window.addEventListener('pageshow', function (e) {
      const confirmToMsgModal = document.getElementById('ConfirmToMsgModal')
      if (confirmToMsgModal) {
        location.reload(true)
      }
    })
  },
  destroyed () {
    this.showConfirmToMsg = false
    this.$store.dispatch('resetDetailStatus')
  },
  mounted () {
    this.pageTopHeight = this.$refs.top.clientHeight + 15
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



