<template>
  <div v-show="status === 1">
    <ul class="calendar-sheet calendar-top">
      <li class="sheet-row">
        <ul class="sheet-header">
          <li>日期</li>
          <li>空腹</li>
          <li>早餐后</li>
          <li>午餐前</li>
          <li>午餐后</li>
          <li>晚餐前</li>
          <li>晚餐后</li>
          <li>睡前</li>
        </ul>
      </li>
    </ul>
    <ul class="calendar-sheet"
      :class="currentTag ? (currentTag === '极低血糖' ? 'very-low-wrp'
        :currentTag === '高血糖' ? 'high-wrp' : 'low-wrp') : ''">
      <li class="sheet-row" v-for="(date, index) in monthDates" :key="index">
        <ul>
          <li>{{date.text}}</li>
          <li v-for="i in timeRanges" :key="i"
            :class="data && data[date.time] && data[date.time][i] && data[date.time][i][0] ?
              (data[date.time][i][0].high ? 'high'
                : (data[date.time][i][0].low && data[date.time][i][0].veryLow ? 'low very-low'
                  : (data[date.time][i][0].low ? 'low'
                    : data[date.time][i][0].veryLow ? 'very-low' : '')) ) : ''"
            :style="{color:data && data[date.time] && data[date.time][i] && data[date.time][i][0] ? data[date.time][i][0].color : ''}">
            {{data && data[date.time] && data[date.time][i] && data[date.time][i][0] ? data[date.time][i][0].Value : ''}}
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'CalendarSheet',
  props: ['data', 'currentTag', 'enrollDaysNum'],
  components: {},
  data () {
    return {
      timeRanges: ['0', '1', '2', '3', '4', '5', '6']
    }
  },
  computed: {
    monthDates: function () {
      let arr = []
      const now = new Date().getTime()
      const days = this.enrollDaysNum < 28 ? this.enrollDaysNum : 28
      for (var i = 0; i < days; i++) {
        const date = new Date(now - i * 24 * 60 * 60 * 1000)
        const dateObj = {
          time: date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate(),
          text: (date.getMonth() + 1) + '/' + date.getDate()
        }
        arr.push(dateObj)
      }
      return arr
    },
    status: function () {
      return this.$store.getters.calendarSendStatus
    }
  },
  methods: {},
  watch: {
    status: function (val, oldVal) {
      if (val) {
        this.monthDates.forEach(date => {
          if (this.data[date.time] && this.data[date.time].length) {
            this.data[date.time] = _.groupBy(this.data[date.time], 'TimeRange')
          }
        })
      }
    }
  },
  mounted: function () {
    if (this.status) {
      this.monthDates.forEach(date => {
        if (this.data[date.time] && this.data[date.time].length) {
          this.data[date.time] = _.groupBy(this.data[date.time], 'TimeRange')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>



